@tailwind base;
@tailwind components;
@tailwind utilities;

/* custom docs CSS */

#TableOfContents > ul {
    @apply overflow-x-hidden text-gray-500 font-medium;
}

#TableOfContents > ul > li > a {
    @apply block py-2 transition-colors duration-200 hover:text-gray-900 font-medium;
}

#TableOfContents > ul > li > ul {
	@apply pl-4;
}

#TableOfContents > ul > li > ul > li {
	@apply pb-2;
}

.highlight {
	@apply overflow-hidden;
}

#mainContent > h2 {
	@apply text-2xl font-bold mt-8 mb-4;
}

#mainContent > h3, #mainContent > h4 {
	@apply text-xl font-bold mt-8 mb-4;
}

#mainContent > p {
	@apply text-base font-normal text-gray-600 mb-4;
}

#mainContent > p > a,
#mainContent > ul > li > a,
#mainContent > ol > li > a {
	@apply text-blue-600;
}

#mainContent > p > a:hover,
#mainContent > ul > li > a:hover,
#mainContent > ol > li > a:hover {
	@apply underline;
}

#mainContent > p > code,
#mainContent > ul > li > code,
#mainContent > ol > li > code {
	@apply font-mono text-blue-600 text-sm px-1;
}

#mainContent > ul {
    list-style: disc;
}

#mainContent > ol {
    list-style: decimal;
}

#mainContent > ul,
#mainContent > ol {
    padding-left: 1rem;
}

#mainContent > ul > li,
#mainContent > ol > li {
    @apply text-base font-normal text-gray-600 mb-4;
}

/* prism styles */

code[class*="language-"],
pre[class*="language-"] {
	color: #c5c8c6;
	text-shadow: 0 1px rgba(0, 0, 0, 0.3);
	font-family: Menlo,ui-monospace,SFMono-Regular,Monaco,Consolas,Liberation Mono,Courier New,monospace;
	direction: ltr;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	line-height: 1.5;

	-moz-tab-size: 4;
	-o-tab-size: 4;
	tab-size: 4;

	-webkit-hyphens: none;
	-moz-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;

    @apply font-light;
}

/* without preview */
.highlight pre[class*="language-"] {
	overflow: auto;
	@apply rounded-xl bg-gray-800 p-6 m-0 text-sm mb-8 mt-4;
}

/* when after preview */
.code-preview + .highlight pre[class*="language-"] {
	overflow: auto;
	@apply rounded-t-none mt-0;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
	background: #1d1f21;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
	padding: .1em;
	border-radius: .3em;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
	color: #7C7C7C;
}

.token.punctuation {
	color: #c5c8c6;
}

.namespace {
	opacity: .7;
}

.token.property,
.token.keyword,
.token.tag {
	color: #96CBFE;
}

.token.class-name {
	color: #FFFFB6;
	text-decoration: underline;
}

.token.boolean,
.token.constant {
	color: #99CC99;
}

.token.symbol,
.token.deleted {
	color: #f92672;
}

.token.number {
	color: #FF73FD;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
	color: #A8FF60;
}

.token.variable {
	color: #C6C5FE;
}

.token.operator {
	color: #EDEDED;
}

.token.entity {
	color: #FFFFB6;
	cursor: help;
}

.token.url {
	color: #96CBFE;
}

.language-css .token.string,
.style .token.string {
	color: #87C38A;
}

.token.atrule,
.token.attr-value {
	color: #F9EE98;
}

.token.function {
	color: #DAD085;
}

.token.regex {
	color: #E9C062;
}

.token.important {
	color: #fd971f;
}

.token.important,
.token.bold {
	font-weight: bold;
}

.token.italic {
	font-style: italic;
}

code.language-bash,
pre.language-bash {
    color: white;
}

code.language-javascript {
    color: white;
}

code.language-javascript .token {
    color: #96CBFE;
    background: transparent;
}

code.language-javascript .token.comment {
    color: #c5c8c6;
}

